import React, { Component } from 'react';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';

// Source : https://www.npmjs.com/package/google-maps-react

export class MapContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
          showingInfoWindow: false,
          activeMarker: {},
          selectedPlace: {}
        }
        // this.onMarkerClick = this.onMarkerClick.bind(this);
        // this.onMapClicked = this.onMapClicked.bind(this);
        // this.handleMapMount = this.handleMapMount.bind(this)
      }
     
    onMarkerClick = (props, marker, e) =>
    this.setState({
        selectedPlace: props,
        activeMarker: marker,
        showingInfoWindow: true
    });
    
    onMapClicked = (props) => {
    if (this.state.showingInfoWindow) {
        this.setState({
        showingInfoWindow: false,
        activeMarker: null
        })
    }
    };

    // handleMapMount(mapProps, map) {
    //     this.map = map;
    
    //     //log map bounds
    //     setTimeout(() => {
    //       console.log(this.map.getBounds());
    //      }, 100)
    //   }
    
        
    render() {
        const style = {
            width: '100%',
            height: '100%',
            flexWrap: 'nowrap'

        }
        const {selectedMarker} =  this.props

        return (

                <Map
                    google={this.props.google}
                    style={style}
                    initialCenter={{
                        lat: 48.8655747,
                        lng: 2.3209916
                    }}
                    className={'map'}
                    zoom={13.2}
                    onClick={this.onMapClicked}
                >
                
                {selectedMarker.map(data =>
                    <Marker
                    key={data.key}
                    title={data.title}
                    name={data.name}
                    position={data.position}
                    onClick = { this.onMarkerClick } />
                )}
                    

                    <InfoWindow
                        marker={this.state.activeMarker}
                        onOpen={this.windowHasOpened}
                        onClose={this.windowHasClosed}
                        visible={this.state.showingInfoWindow}>
                            <div>
                                <div className="inofwindow-title">{this.state.selectedPlace.title}</div>
                                <div className="inofwindow-name">{this.state.selectedPlace.name}</div>
                            </div>
                    </InfoWindow>
                    
                

                </Map>
        );
    }
}
 
export default GoogleApiWrapper({
  apiKey: ('AIzaSyDwQK0R7NiLlRqbv__6ylRdtkhJhKAmXyU')
})(MapContainer)



